.button {
  display: block;
  border-radius: 20px;
  border: 0;
  padding: 7px 14px;
  background-color: $white;
  color: $accent;
  font-size: 8px;
  line-height: 1.25;
  cursor: pointer;
  transition: background-color $transition-argument;

  &:hover,
  &:focus {
    background-color: $button-hover-grey;
  }

  @include for-size(desktop) {
    padding: 12px 20px;
    font-size: 14px;
  }
}

.button-accent {
  background-color: $accent;
  color: $white;
  transition: background-color $transition-argument;

  &:hover,
  &:focus {
    background-color: $button-hover-red;
  }
  @include for-size(desktop) {
    padding: 12px 23px;
  }
}
